*{
    overflow: hidden;
}
@media only screen
and (min-device-width : 320px)
and (max-device-width : 480px) {
    /* STYLES GO HERE */

    body{
        background: #2f3543;
        background: linear-gradient(90deg, #2f3543 0%, #3f4657 100%) no-repeat;
    }
    .column-3{
        width:75%;
        float: left;
    }
    .column-2{
        width:50%;
        float: left;
    }
    .column-1{
        width:25%;
        float: left;
    }
    
    .calc-button{
        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        width:100%;
        height:100px;
        border: none;
        color:#bebebe;
        background-color:transparent;
        border: 1px solid rgba(255, 255, 255, 0.025);
        font-size: 30px;
    }
    .calc-button:hover{
        width:100%;
        height: 100px;
        position: relative;
        background-color: rgba(194, 194, 194, 0.178);
        -webkit-box-shadow: 0px 10px 17px -5px rgba(0,0,0,0.47);
        -moz-box-shadow: 0px 10px 17px -5px rgba(0,0,0,0.47);
        box-shadow: 0px 10px 17px -5px rgba(0,0,0,0.47);
        font-size: 35px;
        color: #bebebe;
        
    }

    .result{
        color: white;
        background: rgb(146,136,245);
        background: linear-gradient(230deg, rgba(146,136,245,1) 0%, rgba(106,99,242,1) 100%);
        position: relative;
        width:100%;
        height:275px;
        float:right;
        border: none;
        text-align: right;
        font-size: 30px;
        border: none;
        max-width: 100vw;
        padding-right:50px ;
        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    }
    .floaty-last{
        position: absolute;
        right: 5%;
        z-index: 99;
        top: 1%;
        font-family: 'Courier New', Courier, monospace;
        color: rgb(233, 233, 233);
    }
}
